nav{
    width: 100%;
    height: auto;
    margin-top: -700px;
    display: flex;
}

        nav a img{
            border-radius:50% ; 
        }
            nav a img:hover{
                transform: scale(1.2);
                cursor: pointer;
                transition: 0.5s;
    }

nav ul {
    list-style: none;
}
    nav ul li{
        list-style: none;
        display: inline-block;
        margin:0 8px;
    }
        nav div ul li a {
            color: #B89790;
        }

    nav div ul li a:hover {
      cursor: pointer;
      color: #60ab90;
      font-weight: 600;
      text-decoration: line-through; }
  
